.bienvenidos {
    text-align: center;
}

.marco h1 {
    display: inline-block;
    position: relative;
    //background: red;
    text-align: center;
    margin: 3rem 0 0 0;
    font-size: 2rem;
    line-height: 1;
    font-weight: $mediana;

    @include tablet {
        margin: 6rem 0 0 0;
        font-size: 3rem;
    }
}

@include tablet {
    .marco h1::after, .marco h1::before {
        content: '';
        position: absolute;
        width: 20rem;
        height: 2px;
        background-color: $apple_silver;
        top: 0.5em;
    }

    .marco h1::before {
        left: -22rem;
    }

    .marco h1::after {
        right: -22rem;
    }
}


.bienvenidos h2 {
    margin: 0 0 3rem 0;
    font-size: 1.3rem;
    line-height: 1.75rem;
    font-weight: $regular;
    padding: 1.5rem;

    @include tablet {
        font-size: 1.8rem;
    }
}

.cursiva {
    font-style: oblique;
}


.slider {
    width: 100%;
    height: auto;
    margin: auto;
    overflow: hidden;
    background-color: $azul_ligero;
    color: $apple_gray;
}

.slider .slider-track {
    display: flex;
    animation: scroll 40s linear infinite;
    -webkit-animation: scroll 40s linear infinite;
    width: calc(200px * 14);
}

.slider .slide {
    width: auto;
    padding: .8rem 0;
}

.slider .slide span {
    
    margin: 0 .5rem;
    width: 100%;
}

.slider .slide strong {
    font-size: 1.4rem;
    margin: 0 .5rem;
    width: 100%;
    font-weight: $bold;
    line-height: 3;
}

@keyframes scroll {
    0% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }

    100% {
        -webkit-transform: translateX(calc(-200px * 6));
        transform: translateX(calc(-200px * 6));
    }
}

.sobre-nosotros {
    min-height: 85vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-seccion {
    background: url('/img/fondo_arquitectonica.jpg') no-repeat left;
    background-size: 55%;
    overflow: hidden;
    padding: 3rem 0;

    @include tablet {
        margin-left: 10rem;
    }
}

.inner-container {
    width: 55%;
    float: right;
    background-color: $cultured_white;
    padding: 3rem;
    border-radius: 15px;

    @include desktop {
        margin-right: 10rem;
    }
}

.inner-container h1 {
    margin-bottom: 3rem;
    font-weight: 900;
    color: $apple_gray;
    margin-top: 0;
}

.texto-nosotros {
    color: $apple_gray;
    //line-height: 3rem;


    @include tablet {
        text-align: left;
        //font-size: 1.8rem;
    }
}

.skills {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 700;
    font-size: .8rem;
    margin-top: 3.5rem;

    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        //font-size: 1.8rem;
    }
}

@keyframes mover {
    0% {
        transform: traslate(100%);
    }
}

@media screen and (max-width:1200px) {
    .inner-container {
        padding: 8rem;
    }
}

@media screen and (max-width:1000px) {
    .about-seccion {
        background-size: 100%;
        padding: 10rem 4rem;
    }

    .inner-container {
        width: 100%;
    }
}

@media screen and (max-width:600px) {
    .about-seccion {
        padding: 0;
    }

    .inner-container {
        padding: 6rem;
    }
}