﻿.efecto-scroll {
    view-timeline-name: --image;
    view-timeline-axis: block;
    animation-timeline: --image;
    animation-name: show;
    animation-range: entry 25% cover 40%;
    animation-fill-mode: both;
}

.negrita {
    font-weight: 800;
}

.sangria{
    margin-left: 1.5rem;
}

.centrar-texto{
    text-align: center;
    margin-bottom: 3rem;
    margin-top: 3rem;
}

.mod-icon {
    width: 2rem;
}

.modifica-icono {
    display: flex;
}
.modifica-icono svg {
    
    width: 2rem;
    height: 2rem;
    color: $azul_medio;
}