﻿.btn {
    font-weight: 600;
    padding: 12px 30px;
    border-radius: 30px;
    color: #ffffff;
    //background: #2f2f2f;
    background: $azul_medio;
    border-color: $azul_medio;
}

.btn:hover {
    color: $blanco;
    //background: #222222;
    //border-color: #222222;
    background: $azul_oscuro;
    border-color: $azul_oscuro;
}

.btn:active, .btn:focus {
    outline: none !important;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.btn.btn-primary {
    background: #3b5d50;
    border-color: #3b5d50;
}

.btn.btn-primary:hover {
    background: #314d43;
    border-color: #314d43;
}

.btn.btn-secondary {
    color: #2f2f2f;
    background: #f9bf29;
    border-color: #f9bf29;
}

.btn.btn-secondary:hover {
    background: #f8b810;
    border-color: #f8b810;
}

.btn.btn-white-outline {
    background: transparent;
    border-width: 2px;
    border-color: rgba(255, 255, 255, 0.3);
}

.btn.btn-white-outline:hover {
    border-color: white;
    color: #ffffff;
}

a {
    text-decoration: none;
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
    color: #2f2f2f;
    //text-decoration: underline;
}

a:hover {
    color: #2f2f2f;
    text-decoration: none;
}

a.more {
    font-weight: 600;
}
