.edita-header {
    /*background: #3b5d50;
    background-image: url(/img/edificio-gris.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 90vh;*/
    background: $azul_oscuro;
    padding: calc(4rem - 30px) 0 0rem 0;
}



@media (min-width: 768px) {
    .edita-header {
        padding: calc(4rem - 30px) 0 4rem 0;
    }
}

@media (min-width: 992px) {
    .edita-header {
        padding: calc(8rem - 30px) 0 8rem 0;
    }
}

.edita-header .intro-excerpt {
    position: relative;
    z-index: 4;
}

@media (min-width: 992px) {
    .edita-header .intro-excerpt {
        max-width: 450px;
    }
}

.edita-header h1 {
    font-weight: 700;
    color: #ffffff;
    margin-bottom: 30px;
}

@media (min-width: 1400px) {
    .edita-header h1 {
        font-size: 54px;
    }
}

.edita-header p {
    color: rgba(255, 255, 255, 0.5);
    margin-botom: 30px;
}

.edita-header .edita-header-img-wrap {
    position: relative;
}

.edita-header .edita-header-img-wrap img {
    position: relative;
    top: 0px;
    right: 0px;
    z-index: 2;
    max-width: 180px;
    left: -20px;
}

@media (min-width: 768px) {
    .edita-header .edita-header-img-wrap img {
        right: 0px;
        left: -100px;
    }
}

@media (min-width: 992px) {
    .edita-header .edita-header-img-wrap img {
        left: 0px;
        top: -80px;
        position: absolute;
        right: -50px;
    }
}

@media (min-width: 1200px) {
    .edita-header .edita-header-img-wrap img {
        left: 0px;
        top: -80px;
        right: -100px;
    }
}

.edita-header .edita-header-img-wrap:after {
    content: "";
    position: absolute;
    width: 255px;
    height: 217px;
    background-image: url("/img/dots-light.svg");
    background-size: contain;
    background-repeat: no-repeat;
    right: -100px;
    top: -0px;
}

@media (min-width: 1200px) {
    .edita-header .edita-header-img-wrap:after {
        top: -40px;
    }
}
