﻿.edita-navbar {
    //background: #3b5d50 !important;
    background: $azul_oscuro !important;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
}

.edita-navbar .navbar-brand {
    font-size: 32px;
    font-weight: 600;
}


.edita-navbar .navbar-brand > span {
    opacity: .4;
}

.edita-navbar .navbar-toggler {
    border-color: transparent;
}

.edita-navbar .navbar-toggler:active, .edita-navbar .navbar-toggler:focus {
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: none;
}

@media (min-width: 992px) {
    .edita-navbar .edita-navbar-nav li {
        margin-left: 15px;
        margin-right: 15px;
    }
}

.edita-navbar .edita-navbar-nav li a {
    font-weight: 500;
    color: #ffffff !important;
    opacity: .5;
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
    position: relative;
}

@media (min-width: 768px) {
    .edita-navbar .edita-navbar-nav li a:before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 8px;
        right: 8px;
        background: #e2e2e2;
        height: 5px;
        opacity: 1;
        visibility: visible;
        width: 0;
        -webkit-transition: .15s all ease-out;
        -o-transition: .15s all ease-out;
        transition: .15s all ease-out;
    }
}

.edita-navbar .edita-navbar-nav li a:hover {
    opacity: 1;
}

.edita-navbar .edita-navbar-nav li a:hover:before {
    width: calc(100% - 16px);
}

.edita-navbar .edita-navbar-nav li.active a {
    opacity: 1;
}

.edita-navbar .edita-navbar-nav li.active a:before {
    width: calc(100% - 16px);
}

.edita-navbar .edita-navbar-cta {
    margin-left: 0 !important;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
    flex-direction: row;
}

@media (min-width: 768px) {
    .edita-navbar .edita-navbar-cta {
        margin-left: 40px !important;
    }
}

.edita-navbar .edita-navbar-cta li {
    margin-left: 0px;
    margin-right: 0px;
}

.edita-navbar .edita-navbar-cta li:first-child {
    margin-right: 20px;
}
