﻿/*--------------------------------------------------------------
# Our Team
--------------------------------------------------------------*/
.team .member {
    text-align: center;
    margin-bottom: 20px;
    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
    padding: 30px 20px;
    background: #fff;
}

.team .member img {
    max-width: 60%;
    border-radius: 50%;
    margin: 0 0 30px 0;
}

.team .member h4 {
    font-weight: 700;
    margin-bottom: 2px;
    font-size: 18px;
}

.team .member span {
    font-style: italic;
    display: block;
    font-size: 13px;
}

.team .member p {
    padding-top: 10px;
    font-size: 14px;
    font-style: italic;
    color: #aaaaaa;
}

.team .member .social {
    margin-top: 15px;
    align-content: center;
    
    
}

.team .member .social a {
    //color: #92cac3;
    color: $azul_medio;
    transition: 0.3s;
}

.team .member .social a:hover {
    //color: #3c8b81;
    color: $azul_oscuro;
}

.team .member .social svg {
 
    height: 1.2rem;
    margin: 0 2px;
}
