﻿.mas-nosotros {
    display: block;
    gap: 15rem;
    margin-top: auto;
    width: 100%;
    height: 25rem;
    color: $blanco;
    margin-bottom: 3rem;

    @include tablet {
        display: flex;
        justify-content: space-between;
        padding: 5vh;
        height: 100%;
    }
}



.mas-izquierda h2 {
    font-size: 2rem;
    font-weight: 600;
    padding-top: 8rem;
    text-align: center;

    @include tablet {
        font-size: 2rem;
        font-weight: $regular;
        padding-top: 3rem;
    }
}

.mas-derecha p {
    display: none;

    @include tablet {
        display: block;
        max-width: 60rem;
        text-align: center;
        color: $blanco;
        font-weight: 400;
        line-height: 2;
        word-spacing: .5rem;
        font-size: 1.5rem;
        font-weight: $mediana;
    }
}

.imagen-fondo {
    background: url('../../img/ladrillo.jpg') no-repeat;
    background-size: cover;
    background-attachment: fixed;
}
